











































































import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue";
import service from '../../service.js';
import sharebtn from '../../components/shareBox.vue';
export default{
	data(){
		return {
			currentpage: 0,
			// 下拉刷新
			mescroll: null, //mescroll实例对象
			// 下拉刷新的配置
			downOption: { 
				use: true, // 是否启用下拉刷新; 默认true
				auto: true, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
			},
			// 上拉加载的配置
			upOption: {
				use: true, // 是否启用上拉加载; 默认true
				auto: true, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
				isLock: false,//是否锁定上拉加载(设为true,可用于不触发upCallback,只保留回到顶部按钮的场景)
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 10 // 每页数据的数量,默认10
				},
				noMoreSize: 3, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				}
			},
			"list": [],
			index:-1,
			currentindex:0,
		}
	},
	filters:{
		formateDate(val){			
				const date = new Date(val);
				const year = date.getFullYear()
				const month = date.getMonth()>9?date.getMonth()+1:`0${date.getMonth()+1}`;
				const day = date.getDate()>9?date.getDate():`0${date.getDate()}`; 
				// console.log(`${year}-${month}-${day}`);
				return `${year}-${month}-${day}`;
		}
	},
	components:{
			sharebtn
	},
	methods:{
		gotoDetail(id){
			console.log('go to detail:',id)
			uni.showModal({
				title:"提示",
				content:"查看案例详情，是否继续？",
				success(){
					uni.showToast({
						title:'下载APP.....'
					})
				}
			})
		},
		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},
		/*下拉刷新的回调, 有三种处理方式: */
		downCallback(mescroll){
			
			// 第1种: 请求具体接口
			// uni.request({
			// 	url: 'xxxx',
			// 	success: () => {
			// 		// 成功隐藏下拉加载状态
			// 		mescroll.endSuccess()
			// 	},
			// 	fail: () => {
			// 		// 失败隐藏下拉加载状态
			// 		mescroll.endErr()
			// 	}
			// })
			// this.initData();
			// 第2种: 下拉刷新和上拉加载调同样的接口, 那以上请求可删, 直接用mescroll.resetUpScroll()代替
			console.log('downCallback')
			
			mescroll.resetUpScroll(); // 重置列表为第一页 (自动执行 page.num=1, 再触发upCallback方法 )
			// 第3种: 下拉刷新什么也不处理, 可直接调用或者延时一会调用 mescroll.endSuccess() 结束即可
			// mescroll.endSuccess()
		},
		/*上拉加载的回调*/
		upCallback(mescroll) {
			// console.log('上拉加载的回调函数')
			let self = this;		
			
			uni.request({
				url:service.BASEURL+'/home/share/high_seas',
				method:"POST",
				success(res) {
					if (res.data && res.data.code == 1) {
						// 获取数据正常
						self.currentpage = res.data.data.page;
						self.list = res.data.data.list;
						
						// 接口返回的当前页数据列表 (数组)
						let curPageData = res.data.data.list; 
						// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
						let totalPage = res.data.data.page; 
						
						// 成功隐藏下拉加载状态
						// 方法一(推荐): 后台接口有返回列表的总页数 totalPage
						mescroll.endByPage(curPageData.length, totalPage); 
						
						// 设置列表数据
						if (mescroll.num == 1) self.booklist = []; // 如果是第一页需手动制空列表
						self.list = self.list.concat(curPageData); // 追加新数据
					}else{
						mescroll.endByPage(0, 0); 
					}
				}
			})
		}
	},
	components:{
		MescrollUni,
	}
	
}
